    transform: scale(1.2);
    transition: all 300ms;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}

.MoMoSocials:hover {
    transform: scale(1.4) rotate(10deg);
    filter: brightness(200%);
   
    animation-iteration-count: infinite;
    
}





.forGiro {
    height: 100px;
    position: fixed;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end ;
}

.Giro {
    border-radius: 100%;
    width: 40em;
    height: 40em;
    position: absolute;
    border: 2px dashed cyan;
    margin-left: auto;
    margin-right: auto;
    left: 0%;
    right: 0;
    top: 0%;
    z-index: 5;
    animation-name: giro1;
    animation-duration: 150000ms;
    animation-iteration-count: infinite;
    backdrop-filter: blur(3px); 
    animation-timing-function: linear;
}
.Giro2 {
    border-radius: 100%;
    width: 35em;
    height: 35em;
    position: absolute;
    border: 5px dotted rgb(136, 70, 40);
    margin-left: auto;
    margin-right: auto;
    left: 0%;
    right: 0;
    top: 25%;
    z-index: 2;
    animation-name: giro2;
    animation-duration: 100000ms;
    animation-iteration-count: infinite;
    backdrop-filter: blur(3px);
    animation-timing-function: linear; 
}

@keyframes giro1 {
    0%   {
        transform: rotate(0deg);
       
      
}

100%  {
    transform: rotate(360deg);

}
}

@keyframes giro2 {
    0%   {
        transform: rotate(0deg);
       
      
}

100%  {
    transform: rotate(-360deg);

}
}

